.pane {
    border-radius: 12px;
    padding: 10px 16px;

    > div {
        display: flex;
        overflow: hidden;

        > div:first-of-type {
            flex-shrink: 0;
            opacity: 0.8;
            overflow: hidden;
            padding: 0 10px 0 0;
        }

        > div:last-of-type {
            flex-shrink: 1;
            text-align: right;
            align-self: center;
            font-weight: 500;
            width: 100%;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }
}

.products {
    > div {
        margin: 0 0 4px 0;

        &:last-of-type {
            margin: 10px 0 0 0;

            > div:first-of-type {
                font-weight: 600;
                align-self: flex-end;
            }

            > div:last-of-type {
                font-weight: 900;
                font-size: 18px;
            }
        }

        > div:first-of-type {
            flex-shrink: 1;
            opacity: 1;
            overflow: hidden;
            padding: 0 10px 0 0;
            width: 100%;
        }

        > div:last-of-type {
            flex-shrink: 0;
            text-align: right;
            align-self: flex-start;
            font-weight: 600;
            width: auto;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }
}

.messengers {
    padding: 8px;

    > div {
        margin: 0 !important;
        display: block;
        overflow: visible;
    }
}

.sectionHeading {
    margin: 0;
    padding: 10px 16px 5px 16px;
    font-weight: 600;
    font-size: 18px;
}

.buttons {
    margin-top: 16px;
    overflow: hidden;

    > * {
        margin: 0 0 8px 0;

        &:last-child {
            margin: 0;
        }
    }
}

.paymentButtons > * {
    margin: 0 0 16px 0;

    &:last-child {
        margin: 0;
    }
}

.cancelPrompt {
    text-align: center;
    margin-bottom: 16px;
}
